#header {
  .logo {
    width: 150px;
    img {
      width: 100%;
    }
  }
  position: absolute;
  padding: 30px 0;
  box-sizing: border-box;
  top: 0;
  z-index: 1000;
  width: 100%;
  .header-navbar {
    width: 500px;
    display: flex;
    justify-content: space-between;
    a {
      color: #fff;
    }
  }
  #languages {
    height: 20px;
  }
  .header-container {
    display: flex;
    justify-content: space-between;
  }
  .mobile-header-nav {
    display: none;
  }
  .mobile-menu-button{
    color: #fff;
  }

  @media screen and (max-width: 700px) {
    .logo {
      width: 100px;
    }
    .mobile-header-nav {
      display: block;
    }
    .header-navbar {
      display: none;
    }
  }
}



