.about-us-content {
  width: 100%;
  color: #000;

  .about-us-content-header-wrapper {

    .about-us-content-header{
      color: #fff;
      height: 300px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }

    a {
      color: #fff;
    }

    width: 100%;
    background: url(http://localhost:4000/images/fon-way.jpg) no-repeat center top / 100%;
  }
  .about-us-content-main-wrapper {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
    p, li{
      color: #000;
    }
    .about-us-content-main {
      width: 70%;
      padding: 10px;
      box-sizing: border-box;
    }
    aside {
      .video-container {
        position: relative;
        img, embed {
          width: 100%;
        }
      }
      .youtube-icon {
        cursor: pointer;
        background: red;
        position: absolute;
        top: 42%;
        left: 43%;
      }
      h2 {
        color: #000;
      }
      width: 20%;
    }

  }
}
