.contact-content-wrapper {
  width: 100%;
  p, h2 {
    color: #000;
  }
  .contact-content-header-wrapper {

    .contact-content-header{
      color: #fff;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }

    a {
      color: #fff;
    }

    width: 100%;
    background: url(http://localhost:4000/images/map.jpg) no-repeat center top / 100%;
    height: 300px;
  }
  .contact-content-main-wrapper {
    padding: 20px 0;
    box-sizing: border-box;

    .adress-section, .form-wrapper {
      padding: 10px;
      box-sizing: border-box;
    }
    .adress-section {
      width: 40%;
      .got-to-right {
        text-align: right;
      }
    }
    .form-wrapper {
      width: 40%;
    }
    .contact-content-main{
      display: flex;
      justify-content: space-around;


      .form-wrapper .form{
        max-width: 300px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        .filed {
          margin-bottom: 5px;
        }
      }
    }
  }
}
